<template>
  <!-- 详情页面 -->
  <div class="detail">
    <!-- 导航栏 -->
    <van-nav-bar fixed left-arrow @click-left="onClickLeft">
      <template #right>
        <van-icon :name="liulanqiIcon" size="24" />
        <van-icon class="weixin" :name="weixinIcon" size="24" />
        <van-icon
          @click="showShare = true"
          name="ellipsis"
          color="#000"
          size="24"
        />
      </template>
    </van-nav-bar>
    <!-- banner -->
    <div class="banner">
      <video
        v-if="detailData.vfurl"
        class="auto-img"
        :src="detailData.vfurl"
        autoplay
        controls
      ></video>
      <img v-else class="auto-img" :src="detailData.photo_path" />
    </div>
    <!-- 标题 -->
    <div class="title-box">
      <div class="name">{{ detailData.title }}</div>
      <div class="browse">
        精品 · 浏览 {{ detailData.views_count_text }} · 收藏
        {{ detailData.favo_counts }}
      </div>
    </div>
    <!-- 用户信息 -->
    <div class="user">
      <div class="userInfo">
        <div class="head-img">
          <img class="auto-img" :src="user.user_photo" alt="" />
        </div>
        <div class="name">{{ user.nickname }}</div>
        <div class="lv">lv{{ user.lvl }}</div>
      </div>
      <div class="follow-box interest" @click="interest">关注</div>
    </div>

    <div class="introduce">
      {{ detailData.cookstory }}
    </div>

    <div class="time-box" v-show="detailData.cook_time">
      <div class="left">
        <div class="img">
          <img class="auto-img" :src="detailData.cook_time_image" alt="" />
        </div>
        <div class="time">{{ detailData.cook_time }}</div>
      </div>
      <div class="right">
        <div class="img">
          <img
            class="auto-img"
            :src="detailData.cook_difficulty_image"
            alt=""
          />
        </div>
        <div class="grade">{{ detailData.cook_difficulty }}</div>
      </div>
    </div>
    <!-- 食材清单 -->
    <div class="food-list-box">
      <div class="title-box">
        <div class="title">食材清单</div>
        <div class="addList">加入采购清单</div>
      </div>
      <div class="item" v-for="(item, index) in detailData.major" :key="index">
        <div class="item-name">{{ item.title }}</div>
        <div class="item-count">{{ item.note }}</div>
      </div>
    </div>
    <!-- 营养分析 -->
    <div :class="{ hide: isclass }">
      <div class="chart-box">
        <div class="title-box">
          <div class="title">营养分析</div>
          <div class="detail-box">查看详情</div>
        </div>
        <div class="chart-info">
          <div
            class="chart"
            id="main"
            :style="{ width: '375px', height: '100%' }"
          ></div>
          <div class="heft">
            <div v-for="(w, index) in seriesData" :key="index">
              {{ w.amount }}
            </div>
          </div>
          <div class="chart-text">
            <div class="count">{{ detailData.energy }}</div>
            <div class="unit">千卡</div>
          </div>
        </div>

        <div class="content-box">
          <div class="content-img">
            <img class="auto-img" src="../assets/icon/dengpao.png" alt="" />
          </div>
          <div class="content">这道菜低热量放心吃吧,点击了解更多健康知识！</div>
        </div>
      </div>
    </div>

    <!-- 步骤 -->
    <div class="cooking-steps">
      <div class="title">烹饪步骤</div>
      <div
        class="list"
        v-for="(item, index) in detailData.cookstep"
        :key="index"
      >
        <div class="steps">
          步骤{{ index + 1 }}/{{ detailData.cookstep.length }}
        </div>
        <div class="steps-img">
          <img class="auto-img" :src="item.image" alt="" />
        </div>
        <div class="content">{{ item.content }}</div>
      </div>
      <div class="release">发布于 {{ createTime }}</div>
    </div>
    <!-- 小贴士 -->
    <div class="tips-box" v-show="detailData.tips">
      <div class="title">小贴士</div>
      <div class="tips">{{ detailData.tips }}</div>
    </div>

    <!-- 评论 -->
    <div class="comment-box" v-show="commentData.length !== 0">
      <div class="comment-title-box">
        <div class="title">热门评论</div>
        <div class="count">{{ commentCount }}条评论</div>
      </div>
      <div v-for="(item, index) in commentData" :key="index">
        <div class="comment-content-box">
          <div class="left">
            <div class="img-box">
              <img v-if="item.u.p" class="auto-img" :src="item.u.p" alt="" />
              <img
                v-else
                class="auto-img"
                src="../assets/icon/huli.png"
                alt=""
              />
            </div>
          </div>
          <div class="right">
            <div class="top">
              <div class="info-box">
                <div class="user-box">
                  <div class="userInfo">{{ item.u.n }}</div>
                  <div class="lv">lv.{{ item.u.lvl }}</div>
                </div>
                <div class="city" v-show="item.city">{{ item.city }}</div>
              </div>
              <div class="likes-box">
                <div class="likes-img">
                  <img
                    class="auto-img imgsrc"
                    @click="likes(index)"
                    ref="img"
                    src="../assets/icon/love.png"
                    alt=""
                  />
                </div>
                <div class="text">
                  {{ item.like_count > 0 ? item.like_count : '赞' }}
                </div>
              </div>
            </div>
            <div class="comment">
              {{ item.content[0].c }}
            </div>
            <div class="reply-comment" v-show="item.child_comments.length">
              <div
                class="item"
                v-for="(v, index) in item.child_comments"
                :key="index"
              >
                <span class="username">{{ v.u.n }} : </span>
                <span class="content">{{ v.content[0].c }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 输入评论 -->
    <div class="input-comment">
      <div class="input-box">
        <input
          @keyup.enter="search"
          class="input"
          type="text"
          v-model="inpValue"
          placeholder="说点什么"
        />
      </div>
      <div class="comment-count">
        <div class="comment-img">
          <img class="auto-img" src="../assets/guanzhu/pinglun.png" alt="" />
        </div>
        <div class="count">{{ commentCount }}</div>
      </div>
      <div class="collect-box">
        <div class="collect-img" @click.stop="goLogin">
          <img
            class="auto-img collectimg"
            src="../assets/icon/collect.png"
            alt=""
          />
        </div>
        <div class="collect-text">收藏</div>
      </div>
    </div>

    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="options"
      @select="onSelect"
    />
  </div>
</template>

<script>
import { Toast } from 'vant'
import { Dialog } from 'vant'
import getCurrentCityName from '../util/getUserLocation'
export default {
  name: 'Detail',
  data() {
    return {
      id: '',
      uname: '',
      inpValue: '',
      dqwz: '',
      detailData: {},
      user: {},
      islikes: false,
      likesIndex: '',
      likesSrc: {
        src: require('../assets/icon/love.png'),
        activeSrc: require('../assets/icon/love-active.png'),
      },
      likesCount: 0,
      liulanqiIcon: require('../assets/guanzhu/liulanqi.png'),
      weixinIcon: require('../assets/guanzhu/weixin.png'),
      createTime: '',
      commentCount: '',
      offset: 0,
      limit: 20,
      isclass: false,
      isinterest: false,
      commentData: [],
      seriesData: [],
      color: [],
      showShare: false,
      options: [
        [
          { name: '微信', icon: 'wechat' },
          { name: '朋友圈', icon: 'wechat-moments' },
          { name: '微博', icon: 'weibo' },
          { name: 'QQ', icon: 'qq' },
        ],
        [
          { name: '复制链接', icon: 'link' },
          { name: '分享海报', icon: 'poster' },
          { name: '二维码', icon: 'qrcode' },
          { name: '小程序码', icon: 'weapp-qrcode' },
        ],
      ],
    }
  },
  created() {
    this.id = this.$route.params.id
    this.getDetailData()
    this.getCommentData()
  },

  mounted() {
    this.getCurrentCity()
  },

  methods: {
    getCurrentCity() {
      getCurrentCityName().then((crity) => {
        this.dqwz = crity
      })
    },

    search() {
      
      if (this.$root.isLogin) {
		  let data = localStorage.getItem('data')
      let dataJson = JSON.parse(data)
      this.uname = dataJson.username
        this.commentData.push({
          ac: '#999999',
          at: this.dqwz.name,
          ccc: 0,
          city: this.dqwz.name,
          child_comments: [],
          cmc: 2,
          content: [
            {
              c: this.inpValue,
            },
          ],
          ia: 0,
          id: '7996966',
          like: 0,
          like_count: 0,
          reply_id: 0,
          time: '',
          u: {
            p: require('../assets/icon/head.png'),
            lvl: 1,
            n: this.uname,
          },
        })
		this.commentCount = this.commentCount +1;
		this.inpValue = '';
      }else{
		  Toast('登入后在评论');
		  this.$router.push({ name: "Login" });
	  }
    },

    // 获取数据
    getDetailData() {
      this.axios({
        method: 'get',
        url: 'https://apis.netstart.cn/douguo/recipe/detail/' + this.id,
      })
        .then((result) => {
          if (result.status === 200) {
            this.detailData = result.data.result.recipe
            this.createTime =
              result.data.result.recipe.create_time.split(' ')[0]
            this.user = result.data.result.recipe.user
            this.seriesData = result.data.result.recipe.nutrition_facts

            this.seriesData.forEach((e) => {
              e.name = e.nutrient
              e.value = e.calorie
              this.color.push(e.pie_chart_color)
            })

            if (this.seriesData.length === 0) {
              this.isclass = true
            }
            this.myEcharts()
          }
        })
        .catch((err) => {})
    },

    onClickLeft() {
      this.$router.go(-1)
    },

    // 获取评论数据
    getCommentData() {
      this.axios({
        method: 'get',
        url:
          'https://apis.netstart.cn/douguo/recipe/flatcomments/' +
          this.id +
          '/' +
          this.offset +
          '/' +
          this.limit,
      })
        .then((result) => {
          if (result.status === 200) {
            this.commentData = result.data.result.comments
            this.commentCount = this.commentData.length
          }
        })
        .catch((err) => {})
    },

    // 点赞
    likes(index) {
      let srcimg = document.querySelectorAll('.imgsrc')[index]

      if (this.islikes) {
        this.likesIndex = this.commentData[index].like_count - 1
        this.commentData[index].like_count = this.likesIndex
        srcimg.src = require('../assets/icon/love.png')
        this.islikes = false
      } else {
        this.likesIndex = this.commentData[index].like_count + 1
        this.commentData[index].like_count = this.likesIndex
        srcimg.src = require('../assets/icon/love-active.png')
        this.islikes = true
      }
    },

    //配置图表
    myEcharts() {
      var myChart = this.$echarts.init(document.getElementById('main'))

      var option = {
        legend: {
          orient: 'vertical',
          left: 100,
          top: 15,
        },
        color: this.color,
        series: [
          {
            label: false,
            type: 'pie',
            data: [...this.seriesData],
            center: ['20%', '50%'],
            radius: ['40%', '70%'],
          },
        ],
      }
      myChart.setOption(option)
    },

    // 关注
    interest() {
      let interest = document.querySelector('.interest')
      if (!this.$root.isLogin) {
        this.$router.push({ name: 'Login' })
      } else {
        if (!this.isinterest) {
          interest.innerText = '已关注'
          interest.style.backgroundColor = '#f1f1f1'
          this.isinterest = true
        } else {
          Dialog.confirm({
            title: '提示',
            message: '确定要取消关注ta吗?',
          }).then(() => {
            interest.innerText = '关注'
            interest.style.backgroundColor = '#facc30'
          })
          this.isinterest = false
        }
      }
    },

    onSelect(option) {
      Toast(option.name)
      this.showShare = false
    },

    goLogin() {
      let collectImg = document.querySelector('.collectimg')
      let text = document.querySelector('.collect-text')
      if (!this.$root.isLogin) {
        this.$router.push({ name: 'Login' })
      } else {
        if (collectImg.src === require('../assets/icon/collect.png')) {
          collectImg.src = require('../assets/icon/liangwujiaoxing.png')
          text.innerHTML = '已收藏'
          for(var i = 0;i<this.$root.scjl.length;i++){
            if(this.id !== this.$root.scjl[i]){
              this.$root.scjl.push(this.id);
            }
          }
          Toast('收藏成功')
        } else {
          collectImg.src = require('../assets/icon/collect.png')
          text.innerHTML = '收藏'
          for(var i = 0;i<this.$root.scjl.length;i++){
            if(this.id === this.$root.scjl[i]){
              this.$root.scjl.splice(i,1);
            }
          }
          Toast('取消收藏')
        }
      }
    },
  },
}
</script>

<style lang="less" scoped>
.hide {
  display: none;
}
.detail {
  padding-bottom: 50px;
  .weixin {
    margin: 0 20px;
  }
  .banner {
    padding-top: 46px;
    width: 100%;
  }
  .title-box {
    padding: 10px;
    .name {
      font-weight: bold;
      font-size: 24px;
    }
    .browse {
      font-size: 16px;
      color: #666;
      margin-top: 5px;
    }
  }
  .user {
    padding: 10px;
    display: flex;
    align-items: center;
    .userInfo {
      display: flex;
      align-items: center;
      .head-img {
        width: 35px;
        height: 35px;
        border-radius: 50%;
        overflow: hidden;
      }
      .name {
        font-weight: bold;
        margin: 0 5px;
      }
      .lv {
        font-style: italic;
        color: #facc30;
      }
    }
    .follow-box {
      font-size: 12px;
      font-weight: bold;
      margin-left: auto;
      width: 80px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      border-radius: 15px;
      background-color: #facc30;
    }
  }
  .introduce {
    margin-top: 20px;
    padding: 0 10px;
    font-size: 16px;
    line-height: 25px;
    color: #444;
  }
  .time-box {
    display: flex;
    padding: 20px 30px;
    font-weight: bold;
    .left {
      display: flex;
      align-items: center;
      .img {
        width: 25px;
        height: 25px;
        margin-right: 5px;
      }
    }
    .right {
      margin-left: auto;
      display: flex;
      align-items: center;
      .img {
        width: 25px;
        height: 25px;
        margin-right: 5px;
      }
    }
  }
  .food-list-box {
    .title-box {
      margin: 5px 0;
      display: flex;
      align-items: end;
      .title {
        font-size: 20px;
        font-weight: bold;
      }
      .addList {
        font-size: 16px;
        line-height: 20px;
        color: #699fa2;
        margin-left: auto;
      }
    }
    .item {
      padding: 5px 10px;
      display: flex;
      .item-name {
        color: #699fa2;
      }
      .item-count {
        margin-left: auto;
      }
    }
  }
  .cooking-steps {
    padding: 10px;
    .title {
      font-size: 20px;
      font-weight: bold;
      margin: 10px 0;
    }
    .steps {
      font-weight: bold;
      margin: 5px 0;
    }
    .steps-img {
      border-radius: 10px;
      overflow: hidden;
    }
    .content {
      font-size: 16px;
      line-height: 30px;
      margin-bottom: 10px;
    }
    .release {
      font-size: 14px;
      color: #999;
    }
  }
  .tips-box {
    padding: 10px;
    .title {
      font-size: 20px;
      font-weight: bold;
      margin: 20px 0;
    }
    .tips {
      font-size: 20px;
      line-height: 28px;
    }
  }
  .comment-box {
    padding: 10px;
    .comment-title-box {
      display: flex;
      align-items: end;
      margin-bottom: 20px;
      .title {
        font-size: 24px;
        font-weight: bold;
        color: #141414;
      }
      .count {
        text-align: bottom;
        margin-left: 5px;
        font-size: 18px;
        font-weight: bold;
        color: #407d84;
      }
    }
    .comment-content-box {
      margin-top: 10px;
      margin-bottom: 20px;
      display: flex;
      .left {
        .img-box {
          width: 40px;
          height: 40px;
          border-radius: 50%;
          overflow: hidden;
        }
      }
      .right {
        margin-left: 10px;
        width: 305px;
        .top {
          width: 305px;
          display: flex;
          align-items: center;
          .info-box {
            .user-box {
              display: flex;
              align-items: end;
              .userInfo {
                font-size: 14px;
                font-weight: bold;
              }
              .lv {
                margin-left: 10px;
                font-size: 12px;
                font-style: italic;
                color: #ffc536;
              }
            }
            .city {
              margin-top: 3px;
              font-size: 12px;
              color: #666;
              font-weight: bold;
            }
          }
          .likes-box {
            margin-left: auto;
            display: flex;
            font-size: 14px;
            .likes-img {
              width: 20px;
              height: 20px;
              margin-right: 5px;
            }
          }
        }
        .comment {
          padding: 5px;
          font-size: 12px;
          line-height: 20px;
          color: #141414;
        }
        .reply-comment {
          padding: 10px;
          margin-bottom: 20px;
          width: 285px;
          border-radius: 10px;
          background-color: #fafafa;
          .item {
            font-size: 14px;

            .username {
              font-weight: bold;
            }
            .content {
              color: #666;
            }
          }
        }
      }
    }
  }
  .input-comment {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #f5f5f5;
    height: 50px;
    display: flex;
    align-items: center;
    padding: 0 10px;
    .input-box {
      .input {
        outline: none;
        border: none;
        background-color: #ddd;
        border-radius: 30px;
        padding: 8px 15px;
        font-size: 14px;
      }
    }
    .comment-count {
      display: flex;
      margin: 0 15px;
      .comment-img {
        width: 20px;
        height: 20px;
        margin-right: 5px;
      }
    }
    .collect-box {
      display: flex;
      .collect-img {
        width: 24px;
        height: 24px;
        margin-right: 5px;
      }
    }
  }
  .chart-box {
    .title-box {
      display: flex;
      align-items: end;
      .title {
        font-size: 20px;
        font-weight: bold;
      }
      .detail-box {
        font-size: 16px;
        line-height: 20px;
        color: #699fa2;
        margin-left: auto;
      }
    }
    .chart-info {
      display: flex;
      align-items: center;
      .chart {
        width: 40%;
      }
      .heft {
        width: 20%;
        font-size: 14px;
        line-height: 24px;
        font-weight: bold;
      }
      .chart-text {
        width: 40%;
        text-align: center;
        .count {
          font-size: 20px;
          font-weight: bold;
        }
        .unit {
          color: #999;
        }
      }
    }
    .content-box {
      padding: 10px;
      display: flex;
      align-items: center;
      .content-img {
        width: 30px;
      }
      .content {
        font-weight: bold;
        font-size: 15px;
      }
    }
  }
}
</style>
